:root {
  --monospace-code-font-family: "JetBrains Mono", monospace;
  --space-xsmall: 2px;
  --space-small: 4px;
  --space-medium: 8px;
  --space-large: 12px;
  --space-xlarge: 16px;
  --space-xxlarge: 24px;
  
  --vscode-error-foreground: #f76769;
  --vscode-success-foreground: #17b877;
  --vscode-warning-foreground: #ffb256;
}