$textbox-width: 400px;

.root {
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  user-select: text;
}

.container {
  flex: 1 1 0;
  position: relative;
  padding: 96px 0;
}

// TODO(b/303699366): Refactor this into a component.
.chatOnboarding {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 24px;
}

.chatOnboardingHeroImage {
  width: 64px;
  height: 64px;
  margin-bottom: 16px;
}

.chatOnboardingTitle {
  letter-spacing: 0em;
}

.previewChip {
  margin-bottom: 16px;

  color: var(--vscode-descriptionForeground);
  font-size: 10px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.1em;
  text-align: left;
}

.textbox {
  width: 100%;
  max-width: $textbox-width;
  margin-top: 40px;

  padding: 16px;
  border-radius: 4px;

  border: 1px solid var(--vscode-panel-border);
  background: var(--vscode-panel-background);

  p {
    color: var(--vscode-descriptionForeground);
  }

  .highlight {
    font-weight: bold;
    color: var(--vscode-foreground);
  }
}

.languagePicker {
  margin: 8px 0 40px;
  width: 100%;
  max-width: $textbox-width;
  align-items: center;
  display: inline-flex;

  color: var(--vscode-foreground);
  text-decoration: none !important;

  .languagePickerText {
    margin-left: 4px;
  }
}
