.menu {
  position: fixed;
  border-radius: 4px;
  display: inline-flex;
  flex-direction: column;
  min-width: 180px;
  max-width: 280px;
  background: var(--vscode-menu-background, #000);
  padding: 4px 0;
  border: 1px solid var(--vscode-menu-border);
  box-shadow: 0px 2px 8px var(--vscode-widget-shadow);
  margin: 0;
  z-index: 2;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  animation: popup-menu-enter 0.15s cubic-bezier(0.4, 0, 0.2, 1);
  transform-origin: 50% 0%;

  --menu-side-padding: 16px;

  // used by <hr> and <vscode-divider>
  --divider-background: var(--vscode-menu-separatorBackground, var(--vscode-text-separator-border));

  @keyframes popup-menu-enter {
    from {
      transform: scale(0.9);
      opacity: 0;
    }
    to {
      transform: scale(1);
    }
  }

  hr {
    border: 0;
    border-top: 1px solid var(--divider-background);
    flex: 1;
    margin: 4px 0;
  }
}

.minimizeWidth {
  min-width: 0;
}

.scrim {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  cursor: default;
  z-index: 1;
}

.item {
  width: calc(100% - 8px); // account for side margin
  text-align: left;
  appearance: none;
  color: var(--vscode-menu-foreground, #fff);
  background-color: transparent;
  cursor: pointer;
  border: none;
  user-select: none;
  padding: 4px var(--menu-side-padding);
  border-radius: 3px;
  margin: 0 4px;
  display: grid;
  grid-template:
    'icon label' auto
    'icon subtitle' auto / auto 1fr;
  flex: 1;
  font-family: var(--font-family, inherit);
  font-size: 13px;
  line-height: 18px;
  outline: 1px solid transparent;
  outline-offset: -1px;

  :global(.codicon),
  :global(.monicon) {
    grid-area: icon;
    margin-right: 12px;
    margin-top: calc((18px - 16px) / 2); // center a 16px icon in a 18px space ... i.e. 1px
    color: var(--foreground);
  }

  &.textAlignRight {
    text-align: right;
  }

  &[aria-disabled] {
    cursor: not-allowed;
    opacity: 0.5;
  }

  // we only handle :focus and not :hover, because we focus() the element on hover in JS. otherwise
  // you might see 2 "focused" items ... one that you're hovering on and one that has keyboard focus
  &:not([aria-disabled]):focus {
    outline: 0;
    color: var(--vscode-menu-selectionForeground, #fff);
    background-color: var(--vscode-menu-selectionBackground, #222);
  }
}

.label {
  grid-area: label;
}

.subtitle {
  grid-area: subtitle;
  font-size: 11px;
  margin-top: 2px;
  color: var(--description-foreground);
}
