@import 'react-shared/globals/index.scss';

body {
  cursor: unset;
  padding: 0;
  // for react-tooltip
  --rt-color-dark: var(--vscode-panel-background);
  --rt-color-white: var(--vscode-descriptionForeground);
}

.root {
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  user-select: text;
}

.chatHistoryContainer {
  flex: 1 1 0;
  position: relative;
}

.chatHistory {
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  max-height: 100%;
  padding-top: var(--container-padding);
  box-sizing: border-box;

  hr {
    width: 100%;
    border: transparent;
    border-top: 1px solid var(--vscode-panel-border);
  }
}

.promptContainer {
  display: flex;
  flex-direction: column;
  margin-bottom: 8px;
  margin: 0 var(--container-padding) 8px;
  position: relative;
}

.slashCommandsContainer {
  position: absolute;
  transform: translate(0px, -100%);
  width: 100%;
  color: var(--input-foreground);
  background: var(--input-background);
  border-radius: calc(var(--corner-radius) * 1px);
  border: calc(var(--border-width) * 1px) solid var(--dropdown-border);
  box-sizing: border-box;
  padding: 8px 0;
}

.slashCommand {
  display: flex;
  justify-content: space-between;
  padding: 2px 16px;
  cursor: pointer;

  & > span:last-child {
    color: var(--input-placeholder-foreground);
  }

  &:hover,
  &.selected {
    background-color: var(--background);
  }
}

.promptInputContainer {
  position: relative;
}

.prompt {
  width: 100%;
  // hack VSCodeTextField
  --corner-radius-round: 4;
  --type-ramp-base-font-size: 14px;

  &::part(control) {
    font-size: 14px;
    line-height: 18px; // hard-coded here to fall on a pixel boundary to avoid tiny jumps
    scroll-padding-bottom: calc(var(--design-unit) * 2px + 1px);
    padding-right: 54px;
    max-height: 50vh;
  }
  &::part(control)::placeholder {
    color: var(--vscode-input-placeholderForeground);
  }
}

.promptIconButtons {
  display: flex;
  gap: 6px;
  position: absolute;
  right: 6px;
  bottom: 12px;
}

.stopGeneratingButton {
  color: var(--vscode-errorForeground);
}

.promptDisclaimer {
  margin-top: 2px;
  font-size: 9px;
}

.chatOnboarding {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 64px 24px 48px 24px;
  hr {
    width: 76px;
    border: transparent;
    border-top: 1px solid var(--vscode-panel-border);
    margin: 40px auto;
  }
}

.chatOnboardingHeroImage {
  width: 64px;
  height: 64px;
  margin-bottom: 16px;
}

.chatOnboardingTitle {
  margin-bottom: 16px;
}

.chatOnboardingSubtitle {
  text-align: center;
  margin-bottom: 16px;
}

.interactiveThreadOnboarding {
  border: 1px solid var(--vscode-inputOption-activeBorder);
  max-width: 320px;
  margin: 32px 0;
  border-radius: 3px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  gap: 12px;
  position: relative;

  &:before {
    content: '';
    z-index: -1;
    position: absolute;
    border-radius: inherit;
    inset: 0;
    background: var(--vscode-inputOption-activeBackground);
    opacity: .2;
    pointer-events: none;
  }
}

.starterPromptChip {
  // TODO: button reset mixin
  appearance: none;
  border: 0;
  cursor: pointer;
  font-family: inherit;
  font-size: inherit;

  background-color: var(--vscode-panel-background);
  box-shadow: 0 0 0 1px var(--vscode-panel-border) inset;
  border-radius: 16px;
  display: inline-flex;
  align-items: center;
  padding: 8px 8px 8px 16px;
  gap: 4px;
  color: var(--vscode-textLink-foreground);
  margin-bottom: 8px;
}

.chatMessage {
  padding-block: 16px var(--container-padding);
  padding-inline: var(--container-padding);
  margin-top: -1px;
  border-top: 1px solid var(--vscode-panel-border);

  &:last-child {
    padding-block-end: 0;
  }

  &.isUser {
    background-color: transparent;
  }

  &:not(:where(:hover, :focus-within)) .hideUnlessHovering {
    opacity: 0;
  }
}

.chatMessageHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 8px;
  & > div {
    display: flex;
    align-items: center;
  }
}

.messageAvatar {
  position: relative;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  margin-right: 8px;
  background-color: var(--vscode-editorWidget-background);
  border-radius: 50%;

  img {
    object-fit: cover;
    max-width: 20px;
    max-height: 20px;

    &.messageAvatarUserPhoto {
      border-radius: 999px;
    }
  }
}

.messageSenderName {
  font-weight: 600;
}

.messageBody {
  margin-top: 8px;
  display: flex;
  flex-direction: column;

  p,
  ul,
  ol,
  pre,
  table,
  .codeSnippet {
    margin-bottom: 16px;
  }

  ul,
  ol {
    padding-left: 16px;

    li {
      margin-bottom: 8px;
    }
  }

  code,
  pre {
    font-family: var(--editor-font-family, 'JetBrains Mono', monospace);
    color: var(--vscode-terminal-ansiBrightGreen);
  }

  a code {
    color: unset;
  }

  [type='button'] {
    width: fit-content;
  }

  :last-child {
    margin-bottom: 0;
  }
}

.messageBody table {
  --border-radius: 8px;
  // border-collapse: collapse; // entirely custom borders so we can get corner radii
  border-collapse: separate;
  border-spacing: 0;
  font-size: 90%;
  line-height: 1.3333;

  th,
  td {
    padding: 4px 6px;
    border-top: 1px solid var(--vscode-textSeparator-foreground);
    border-left: 1px solid var(--vscode-textSeparator-foreground);

    &:last-child {
      border-right: 1px solid var(--vscode-textSeparator-foreground);
    }
  }

  thead:first-child tr:first-child,
  > tr:first-child {
    th:first-child,
    td:first-child {
      border-top-left-radius: var(--border-radius);
    }

    th:last-child,
    td:last-child {
      border-top-right-radius: var(--border-radius);
    }
  }

  tbody:last-child tr:last-child,
  > tr:last-child {
    th,
    td {
      border-bottom: 1px solid var(--vscode-textSeparator-foreground);
    }

    th:first-child,
    td:first-child {
      border-bottom-left-radius: var(--border-radius);
    }

    th:last-child,
    td:last-child {
      border-bottom-right-radius: var(--border-radius);
    }
  }
}

.messageActions {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 12px;
  transition: opacity 0.2s ease;
}

.typingIndicator {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: var(--vscode-textLink-foreground);
}

.typingNewMessage,
.suggestedNextPromptContainer {
  flex: 0 0 auto;
  padding: 0 var(--container-padding);
  border-top: 1px solid var(--vscode-panel-border);
  box-sizing: border-box;
  height: 40px;
}

.suggestedNextPromptContainer {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  gap: 8px;
  overflow-x: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &:not(.hasSuggestions) {
    border-color: transparent;
  }

  &::-webkit-scrollbar {
    display: none;
  }
}

.suggestedNextPromptChip {
  // TODO: button reset mixin
  background: transparent;
  appearance: none;
  border: 0;
  cursor: pointer;
  font-family: inherit;
  font-size: 13px;
  line-height: 16px;
  flex: 0 0 auto;
  white-space: nowrap;
  padding: 0;

  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: var(--vscode-textLink-foreground);
  animation: fadeSlideIn .2s ease .5s backwards;

  @keyframes fadeSlideIn {
    from { opacity: 0; transform: translateY(100%); }
    to { opacity: 1; transform: translateY(0); }
  }

  &:hover {
    color: var(--vscode-textLink-activeForeground);
  }
}

.typingDots {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2px;
  background-color: var(--vscode-editor-background);
  border-radius: 50%;

  span {
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: var(--vscode-textLink-foreground);
    animation: pulsedot 2s ease infinite;
    animation-fill-mode: backwards;

    &:nth-child(2) {
      animation-delay: 0.25s;
    }

    &:nth-child(3) {
      animation-delay: 0.5s;
    }
  }
}

.infoIcon {
  vertical-align: middle;
  margin-left: 4px;
}

.tooltip {
  max-width: 400px;
  font-size: 11px;
}

@keyframes pulsedot {
  0% {
    opacity: 0.1;
  }

  1% {
    opacity: 1;
  }

  80% {
    opacity: 0.1;
  }

  100% {
    opacity: 0.1;
  }
}
