.scrollEdges {
  --fade-length: 16px;
}

.standardFadeTop,
.standardFadeBottom {
  $fullOpacityImage: linear-gradient(#000, #000);
  // default to no fade
  --fade-top-image: #{$fullOpacityImage};
  --fade-bottom-image: #{$fullOpacityImage};
  mask-image:
    var(--fade-top-image),
    #{$fullOpacityImage},
    var(--fade-bottom-image);
  mask-repeat: no-repeat;
  mask-size:
    100% var(--fade-length),
    100% calc(100% - var(--fade-length) * 2),
    100% var(--fade-length);
  mask-position:
    top,
    center,
    bottom; 
}

.standardFadeTop {
  --fade-top-image: linear-gradient(0deg, #000, transparent);
}

.standardFadeBottom {
  --fade-bottom-image: linear-gradient(180deg, #000, transparent);
}