@use 'sass:color' as sass_color;

.root {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 8px;
}

.root:not(.onlyShowingActions) {
  // force opacity to 1 when showing more than just actions
  opacity: 1 !important;
}

.feedbackBar {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
}

.feedbackButtons {
  display: flex;
  gap: 4px;
  align-items: center;
}

.feedbackContainer {
  margin-bottom: 8px;
  background-color: var(--vscode-panel-background);
  padding: 16px;
  border-radius: 4px;
  border: 1px solid var(--vscode-panel-border);
}

.feedbackHeader {
  margin-bottom: 8px;
}

.feedbackDisclaimer {
  margin: 8px 0;
}

.checkbox-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.checkboxContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  width: 100%;
}

.feedbackTextArea {
  margin: 8px 0;
  flex: 1 1 0;

  &::part(control)::placeholder {
    color: var(--vscode-input-placeholderForeground);
  }
}

.buttonWrapper {
  margin-top: 4px;
}

.confirmation {
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  gap: 16px;
  color: var(--vscode-foreground);
  border: 1px solid var(--vscode-focusBorder);
  border-radius: 4px;
  background-color: color-mix(in srgb, var(--vscode-focusBorder) 10%, transparent);
}
