.feedbackToken {
  display: flex;
  align-items: center;
  border: 1px solid var(--vscode-panel-border);
  border-radius: 4px;
  padding: 0 8px;
  width: fit-content;
  color: var(--description-foreground);
  font-size: 12px;

  span {
    margin-left: 8px;
  }

  [class*='chatEmbed'] & {
    border: none;
    padding-inline-start: 0;
  }
}
