/* Custom Monicons iconfont */

@font-face {
  font-family: "Monicons";
  src: url("./monicons.woff?2b385ea168b670c1637479cbe55c424d") format("woff");
  font-display: block;
  font-weight: normal;
  font-style: normal;
}

:global {
  .monicon[class*="monicon-"] {
    font: normal normal normal 16px/1 Monicons;
    display: inline-block;
    text-decoration: none;
    text-rendering: auto;
    text-align: center;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    width: 1em;
    height: 1em;
    font-weight: normal;
    font-style: normal;
    text-transform: none;
  }

  .monicon-modifier-disabled {
    opacity: 0.5;
  }

  .monicon-firebase:before { content: "\f101" }
  .monicon-monospace:before { content: "\f102" }
  .monicon-flutter:before { content: "\f103" }
  .monicon-flutter-fill:before { content: "\f104" }
  .monicon-git-fill:before { content: "\f105" }
  .monicon-android:before { content: "\f106" }
  .monicon-html:before { content: "\f107" }
  .monicon-angular:before { content: "\f108" }
  .monicon-react:before { content: "\f109" }
  .monicon-nextjs:before { content: "\f10a" }
  .monicon-git:before { content: "\f10b" }
  .monicon-monospace-alt:before { content: "\f10c" }
  .monicon-ios:before { content: "\f10d" }
  .monicon-rotate-landscape:before { content: "\f10e" }
  .monicon-ai:before { content: "\f10f" }
  .monicon-share:before { content: "\f110" }
  .monicon-perf:before { content: "\f111" }
  .monicon-home:before { content: "\f112" }
  .monicon-spark:before { content: "\f113" }
  .monicon-spark-outline:before { content: "\f114" }
}