.codeSnippet {
  --border-radius: 8px;
  position: relative;

  .preContainer {
    box-sizing: border-box;
    overflow-x: auto;
    overflow-y: hidden;
  }

  &:not(.isBorderless) .preContainer {
    box-shadow: 0 0 0 1px var(--vscode-panel-border) inset;
    border-radius: var(--border-radius);
    background-color: var(--vscode-panel-background);

    > pre {
      padding: 8px 12px;
    }
  }

  .citations {
    border-top: 1px solid var(--vscode-panel-border);
    padding: 8px 12px;
    box-sizing: border-box;
    display: flex;
    gap: 8px;
    align-items: start;
  }

  &.isCollapsed .preContainer {
    cursor: pointer;
    max-height: 100px;
    $maskImage: -webkit-gradient(
      linear,
      left 50%,
      left 100%,
      from(rgba(0, 0, 0, 1)),
      to(rgba(0, 0, 0, 0))
    );
    -webkit-mask-image: $maskImage;
    mask-image: $maskImage;
    opacity: 0.8;
    transition: opacity 0.1s;

    &:hover {
      opacity: 1;
    }
  }

  pre {
    margin: 0;
    font-family: var(--editor-font-family, 'JetBrains Mono', monospace);
    // default color for un-highlighted code (e.g. language couldn't be determined, or language
    // not found in our highlighting languages). this was previously green, but it's jarring to see
    // a huge block of green, so using something more muted (grayish white)
    color: var(--vscode-terminal-ansiWhite);

    :global(span.line:not(:last-child)) {
      min-height: 18px; // hard-coded fallback if lh units aren't supported
      min-height: 1lh; // 1 line height ... this is so that empty lines don't collapse to zero height
    }
  }

  &:hover,
  &:focus-within {
    .codeSnippetToolbar {
      opacity: 1;
    }
  }

  &.isDiff pre {
    display: flex;
    flex-direction: column;
    min-width: fit-content;
  }

  .diffLineSame,
  .diffLineRemoved,
  .diffLineAdded {
    position: relative;
    padding-left: 1em;
    z-index: 1;

    &:before {
      position: absolute;
      content: '';
      left: 0;
    }
  }

  .diffLineAdded,
  .diffLineRemoved {
    &:before {
      content: '+';
    }
    &:after {
      content: '';
      position: absolute;
      inset: 0;
      // TODO: switch to insertedLineBackground once IDX theme has it
      background-color: var(--vscode-diffEditor-insertedTextBackground);
      z-index: -1;
    }
  }

  .diffLineRemoved {
    user-select: none;
  }

  .diffLineRemoved {
    &:before {
      content: '–';
    }
    &:after {
      // TODO: switch to removedLineBackground once IDX theme has it
      background-color: var(--vscode-diffEditor-removedTextBackground);
    }
  }
}

.collapseLink {
  display: inline-flex;
  margin-top: 8px;
}

.codeSnippetToolbar {
  position: absolute;
  top: 1px;
  right: 1px;
  padding: 4px;
  display: flex;
  align-items: center;
  gap: 4px;
  opacity: 0;
  z-index: 1;

  &::before {
    // scrim
    content: '';
    border-top-right-radius: var(--border-radius);
    background-color: var(--vscode-panel-background);
    position: absolute;
    opacity: 0.8;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
  }
}
