h1,
h2,
h3,
h4,
h5,
h6,
.text {
  margin: 0;
  padding: 0;
  font-weight: normal;
  cursor: default;
  user-select: none;

  &[disabled] {
    opacity: 0.4;
  }
}

p,
ol,
ul {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 26px;
  line-height: 32px;
  font-weight: 600; // semibold
  margin: 0;
}

h2 {
  font-size: 16px;
  line-height: 20px;
  font-weight: 500; // medium
}

h3 {
  font-size: 13px;
  line-height: 18px;
  font-weight: 800; // heavy
}

h4 {
  font-size: 11px;
  line-height: 16px;
  font-weight: 700; // bold
  text-transform: uppercase;
}

h5 {
  font-size: 11px;
  line-height: 14px;
  font-weight: 500; // medium
  text-transform: uppercase;
}

h6 {
  font-size: 11px;
  line-height: 14px;
  font-weight: 800; // heavy
}

.b1 {
  font-size: 13px;
  line-height: 18px;
}

.b2 {
  font-size: 12px;
  line-height: 16px;
}

.l1 {
  font-size: 14px;
  line-height: 20px;
  font-weight: 500; // medium
}

.l2 {
  font-size: 12px;
  line-height: 16px;
  font-weight: 700; // bold
}

.l3 {
  font-size: 11px;
  line-height: 16px;
  font-weight: 500; // medium
}

.l4 {
  font-size: 9px;
  line-height: 12px;
  font-weight: 700; // bold
}

.color-secondary {
  color: var(--vscode-descriptionForeground);
}

.selectable {
  cursor: auto;
  user-select: text;
}

.no-cursor {
  cursor: inherit;
}