.debugPanel {
  position: relative;
}

.toggleButton {
  position: absolute;
  right: 4px;
  bottom: 4px;
  z-index: 999;
  color: var(--background);
  background-color: var(--vscode-error-foreground);
  font-weight: 900;
  text-transform: uppercase;
  font-size: 10px;
  border-radius: 3px;
  appearance: none;
  border: 0;
  border-radius: 999px;
  font-family: unset;
  font-size: unset;
  display: flex;
  align-items: center;
  gap: 4px;
  cursor: pointer;
}

.content {
  margin: 2px;
  border-radius: 2px;
  border: 1px solid var(--vscode-error-foreground);
  padding: 4px 40px 4px 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;

  .debugPanel:not(.isVisible) & {
    display: none;
  }

  &:before {
    content: '';
    position: absolute;
    inset: 0;
    background-color: var(--vscode-error-foreground);
    opacity: .1;
    z-index: -1;
  }
}

.panelRow {
  display: flex;
  align-items: center;
  gap: 4px
}

.panelRow vscode-button {
  // ultra compact buttons for debugging area
  --button-padding-horizontal: 4px;
  --button-padding-vertical: 0;
}