.feedbackDropdown {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  max-width: 100%;
  margin-left: -75px;
}

.feedbackDropdownButton {
  cursor: pointer;
  appearance: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  font-weight: inherit;
  color: var(--description-foreground);
  display: flex;
  align-items: center;
  background-color: transparent;
  border: none;
  border-radius: 6px;
  padding-inline-start: 8px;
  padding-block: var(--button-icon-padding);
  transition: background-color 0.2s ease-out, color 0.2s ease-out;

  span {
    font-size: 11px;
    display: inline-block;
    margin-right: 2.5px;
  }

  &:hover {
    color: var(--foreground);
    background-color: var(--vscode-toolbar-hoverBackground);
  }
  &:active {
    color: var(--foreground);
    background-color: var(--vscode-toolbar-activeBackground);
  }

  &[class*='feedbackDropdownOpen'] {
    color: var(--foreground);
    box-shadow: 0 0 0 1px var(--vscode-inputOption-activeBorder) inset;
    background-color: var(--vscode-inputOption-activeBackground);
    margin-block-end: 10px;
    .feedbackDropdownArrow {
      transform: rotate(180deg);
    }
  }
}

.feedbackDropdownList {
  border-radius: 4px;
  border: 1px solid var(--vscode-panel-border);
  padding: 6px 8px;
  max-width: 100%;
  box-sizing: border-box;

  li {
    list-style: none;
    &:hover {
      background-color: var(--vscode-list-hover-background);
    }
  }
  a {
    cursor: pointer;
    display: block;
    transition: color 0.3s ease-out;
    word-break: break-all;
    &:hover {
      color: var(--vscode-list-hover-foreground);
    }
  }
}
