.root {
  min-height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 8px; // default container padding adds to side padding
  user-select: text;
}

.promptDisclaimer {
  margin-top: 2px;
}

.chatOnboarding {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 320px;
}

.chatOnboardingHeroImage {
  width: 80px;
  height: 80px;
  margin-bottom: 16px;
}

.chatOnboardingTitle {
  display: inline-flex;
  margin-bottom: 16px;
}

.previewChip {
  margin-bottom: 16px;

  color: var(--vscode-descriptionForeground);
  font-size: 10px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.1em;
  text-align: left;
}

.chatOnboardingSubtitle {
  text-align: center;
  margin: 24px 0 48px;
}

.checkbox-wrapper {
  margin-top: 24px;
}

.checkboxDescription {
  margin-left: 28px;
}

.ctaWrapper {
  margin-top: 48px;
}
