.toolbar {
  z-index: 10;
  padding: var(--space-xsmall) var(--space-medium);
  min-height: 30px;
  box-sizing: border-box;
  display: flex;
  column-gap: 4px;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  background-color: var(--vscode-editor-background);

  hr {
    width: 1px;
    height: auto;
    align-self: stretch;
    border: 0;
    border-left: 1px solid
      var(--vscode-menu-separatorBackground, var(--vscode-text-separator-border));
    margin:
      2px // extra vert space
      2px; // extra horizontal space
  }
}

.toolbar:not(.isBorderless) {
  border-bottom: 1px solid var(--vscode-panel-border);
}
