:root {
  --container-padding: 20px;
  --description-foreground: var(
    --vscode-descriptionForeground,
    #959595
  ); // for some reason not provided by webview-ui-toolkit
  --line-height: 140%;
}

body {
  margin: 0;
  padding: 0 var(--container-padding);
  color: var(--vscode-foreground, var(--foreground));
  color: var(--vscode-foreground);
  font-size: var(--vscode-font-size, var(--font-size));
  line-height: var(--vscode-line-height, var(--line-height));
  font-weight: var(--vscode-font-weight, var(--font-weight));
  font-family: var(--vscode-font-family, var(--font-family));
  background-color: var(--vscode-editor-background, var(--editor-background));
}

ol,
ul {
  padding-left: var(--container-padding);
}

*:focus-visible {
  outline-color: var(--vscode-focusBorder, var(--focus-border)) !important;
}

a {
  color: var(--vscode-textLink-foreground, var(--link-foreground));
}

a:hover,
a:active {
  color: var(--vscode-textLink-activeForeground, var(--link-active-foreground));
}

code {
  font-family: var(--vscode-editor-font-family, monospace);
}

vscode-link {
  // hack against links always having a 1px border;
  margin: -1px;
}
